.main-footer {
  position: relative;
  margin-bottom: 50px;
}

.main-footer__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 25px;

  @include vp-1199 {
    position: relative;
    gap: 0;
  }

  @include vp-767 {
    padding-bottom: 50px;
  }
}

.main-footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include vp-1199 {
    width: 100%;
    flex-direction: column;
    align-items: end;
  }

  @include vp-767 {
    align-items: center;
  }
}

.main-footer__nav {
  display: flex;
  gap: 52px;

  @include vp-767 {
    gap: 20px;
  }
}

.main-footer__social {
  @include vp-1199 {
    position: absolute;
    bottom: 0;
  }
}

.main-footer__copyright {
  display: flex;
  justify-content: space-between;

  & p {
    margin: 0;
    font-size: $fz-xsmall;
    line-height: $lh-xxxsmall;
  }

  & a {
    font-size: $fz-xsmall;
    line-height: $lh-xxxsmall;
    border-bottom: 1px solid $color-charcoal;
    padding-bottom: 2px;
    width: max-content;

    @include hover-focus {
      outline: none;
      color: $color-cerulean-blue;
      border-bottom: 1px solid $color-cerulean-blue;

      @include active {
        color: $color-endeavour;
        border-bottom: 1px solid $color-endeavour;
      }
    }
  }

  @include vp-1199 {
    margin-top: 25px;
    flex-direction: column;
    gap: 10px;
    justify-content: space-between;

    & a {
      font-size: $fz-small;
      line-height: $lh-small;
    }

    & p {
      order: 2;
      color: rgba($color-charcoal, 0.6);
      font-size: $fz-small;
      line-height: $lh-small;
    }
  }

  @include vp-767 {
    margin-top: 16px;
    align-items: center;

    & a {
      line-height: $lh-xsmall;
    }

    & p {
      line-height: $lh-xsmall;
    }
  }
}
